import Vue from 'vue'
import VueRouter from 'vue-router'



// 路由懒加载
const chat = () => import(/* webpackChunkName: "Login_Home_Welcome" */ '../components/chat.vue')








Vue.use(VueRouter)

const routes = [
  { path: '/',
    component: chat,
    redirect:'',
    meta:{keepalive:true},
}

]

const router = new VueRouter({
  routes
})



export default router
